import { Component, ViewChild, ElementRef, AfterViewInit } from '@angular/core';

// import JQuery from global scope
declare const jQuery: any;

/**
 * Main application component
 */
@Component({
  selector: 'app',
  template: require('./app.component.html'),
  styles: [require('./app.component.sass')]
})
export class AppComponent implements AfterViewInit {

  readonly title: string;

  /**
   * links to HTML elements
   */
  @ViewChild("pushableContext") pushableContext: ElementRef;
  @ViewChild("sidebarToggleButton") sidebarToggleButton: ElementRef;

  /**
   * primary constructor
   */
  constructor() {
    // ...
  }

  /**
   * "AfterIhit" hook
   */
  ngAfterViewInit(): void {

    // enable sidebar through Semantic-UI jquery method
    jQuery('app-sidebar-navigation')
      .sidebar({context: this.pushableContext.nativeElement})
      .sidebar('setting', 'transition', 'overlay')
      .sidebar('attach events', this.sidebarToggleButton.nativeElement)
    ;
  }
}



