import { Routes, RouterModule } from "@angular/router";
import { OpportunitiesComponent } from "./opportunities/opportunities.component";
import { EmployessComponent } from "./employees/employees.component";
import { NgModule } from "@angular/core";
import { AllocationComponent } from "./allocation.component";
import { AuthGuardService } from "../service/authguard.service";


const routes: Routes = [
  
  { path: '', component: AllocationComponent, canActivate: [AuthGuardService], children: [
    { path: '', redirectTo: 'opportunities', pathMatch: 'full' },
    { path: 'opportunities', component: OpportunitiesComponent, pathMatch: 'full' },
    { path: 'employees', component: EmployessComponent, pathMatch: 'full' }
  ]}
  
];

@NgModule({
  imports: [ RouterModule.forChild(routes) ],
  exports: [ RouterModule ],
})
export class AllocationRoutingModule {}




