import { Component, HostBinding } from '@angular/core';
import { smoothTransitionAnimation } from "../../app.animations";

@Component({
  selector: 'employees',
  template: require('./employees.component.html'),
  animations: [ smoothTransitionAnimation ]
})
export class EmployessComponent {
    
  /**
   * enable transition animation when the route of this component becomes active
   */
  @HostBinding('@routeAnimation') routeAnimation = true;
  @HostBinding('style.display')   display = 'block';
  @HostBinding('style.position')  position = 'relative';
}



