import { Component, ViewChild, ElementRef, AfterViewInit } from '@angular/core';
import { smoothTransitionAnimation } from "../app.animations";

// import JQuery from global scope
declare const jQuery: any;

@Component({
  selector: 'app-home-component',
  template: require('./home.component.html'),
  animations: [ smoothTransitionAnimation ]
})
export class HomeComponent implements AfterViewInit {
  
  readonly welcome = "Welcome in GIS application!";
  readonly allocationSheetModule = "Allocation Sheet Module";
  readonly adminModule = "Admin Module";
  
  /**
   * links to HTML elements
   */
  @ViewChild("image1") image1: ElementRef;
  @ViewChild("image2") image2: ElementRef;
  
  /**
   * "AfterIhit" hook
   */
  ngAfterViewInit(): void {
    
    let applyHover = (elem: any) => {
  
      jQuery(elem)
        .hover( () => {
          jQuery(elem).transition('pulse')
        }, () => {
          // second empty function needed for correct hover behavior
        });
    };
    
    applyHover(this.image1.nativeElement);
    applyHover(this.image2.nativeElement);
  }
}


