import { NgModule } from "@angular/core";
import { AllocationComponent } from "./allocation.component";
import { EmployessComponent } from "./employees/employees.component";
import { OpportunitiesComponent } from "./opportunities/opportunities.component";
import { NgSemanticModule } from "ng-semantic";
import { AllocationRoutingModule } from "./allocation.routing";
import { CommonModule } from "@angular/common";


@NgModule({
  imports:      [ CommonModule, NgSemanticModule, AllocationRoutingModule ],
  declarations: [ AllocationComponent, EmployessComponent, OpportunitiesComponent ],
  exports:      [ AllocationComponent ]
})
export class AllocationModule {}


