import { Injectable } from '@angular/core';
import { Subject } from "rxjs";

@Injectable()
export class UserService {

    isLogged = false;

    userEventStream: Subject<UserEvent> = new Subject();

    login(): void {

        console.log("login user");

        this.isLogged = true;
        this.userEventStream.next(new LoginEvent());
    }

    logout(): void {

        console.log("logout user");

        this.isLogged = false;
        this.userEventStream.next(new LogoutEvent());
    }
}

export interface UserEvent {}

export class LoginEvent implements UserEvent {}
export class LogoutEvent implements UserEvent {}



